import React, { Component } from "react";
import classes from "../../../static/css/When.css";
import { Transition, Segment, Grid, GridColumn } from "semantic-ui-react";

export default class When extends Component {
  state = {
    endDate: new Date("4/01/2019").getTime(),
    days: 0,
    daysShuffle: true,
    hours: 0,
    hoursShuffle: true,
    minutes: 0,
    minutesShuffle: true,
    seconds: 0,
    secondsShuffle: true
  };

  componentDidMount() {
    this.timerID = setInterval(() => this.updateTime(), 50);
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  updateTime = () => {
    // get new date
    const time = new Date();
    // set time units
    const hours = time.getHours();
    const minutes = time.getMinutes();
    const seconds = time.getSeconds();

    const timeDiff = Math.abs(this.state.endDate - time.getTime());
    const diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24));
    const days = diffDays;
    // on hour chanage, update hours and shuffle state

    if (days !== this.state.days) {
      const daysShuffle = !this.state.daysShuffle;
      this.setState({
        days,
        daysShuffle
      });
    }
    if (hours !== this.state.hours) {
      const hoursShuffle = !this.state.hoursShuffle;
      this.setState({
        hours,
        hoursShuffle
      });
    }
    // on minute chanage, update minutes and shuffle state
    if (minutes !== this.state.minutes) {
      const minutesShuffle = !this.state.minutesShuffle;
      this.setState({
        minutes,
        minutesShuffle
      });
    }
    // on second chanage, update seconds and shuffle state
    if (seconds !== this.state.seconds) {
      const secondsShuffle = !this.state.secondsShuffle;
      this.setState({
        seconds,
        secondsShuffle
      });
    }
  };

  renderDays = () => {
    return (
      <Transition visible={true} animation="slide up" duration={500}>
        <h1>{this.state.days}</h1>
      </Transition>
    );
  };
  renderHours = () => {
    return (
      <Transition visible={true} animation="slide up" duration={500}>
        <h1>{this.state.hours}</h1>
      </Transition>
    );
  };
  renderMinutes = () => {
    return (
      <Transition visible={true} animation="slide up" duration={500}>
        <h1>{this.state.minutes}</h1>
      </Transition>
    );
  };
  renderSeconds = () => {
    return (
      <Transition visible={true} animation="slide up" duration={500}>
        <h1>{this.state.seconds}</h1>
      </Transition>
    );
  };

  render() {
    // state object destructuring
    const {
      hours,
      minutes,
      seconds,
      hoursShuffle,
      minutesShuffle,
      secondsShuffle
    } = this.state;

    return (
      <div>
        <Grid className={classes.when}>
          <Grid.Column width={3} className={classes.empty} />
          <Grid.Column width={10} verticalAlign="middle">
            <Grid>
              <Grid.Row columns={4}>
                <Grid.Column>
                  <Segment />
                </Grid.Column>
                <Grid.Column>
                  <Segment />
                </Grid.Column>
                <Grid.Column>
                  <Segment />
                </Grid.Column>
                <Grid.Column>
                  <Segment />
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Grid.Column>
          <Grid.Column width={3} className={classes.empty} />
        </Grid>
      </div>
    );
  }
}
